.teaser-section {
  padding: 60px 0;
  color: var(--color-black);
  background-color: var(--color-white);

  &__headline {
    font-size: var(--font-size-xl);
    font-weight: bold;
    text-align: center;
    text-transform: uppercase;
  }

  &__teaser-wrapper {
    margin-left: -15px;
    margin-right: -15px;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }

  &__teaser {
    cursor: pointer;
    position: relative;
    margin-right: 10px;

    &:hover {
      img {
        transform: scale(1.2);
      }
    }
  }

  &__image {
    overflow: hidden;

    img {
      transition: transform 0.5s ease;
    }
  }

  &__image-inner {
    position: absolute;
    bottom: 28px;
    left: 20px;
    right: 20px;
    color: var(--color-white);
  }

  &__image-headline {
    font-weight: 600;
    font-size: 3.2rem;
  }
}
