@mixin link-default {
  color: var(--color-black);
  text-decoration: none;
  transition: color 200ms ease-in-out;

  &:hover {
    color: var(--color-highlight);
  }
}

@mixin cta-white {
  color: var(--color-white);
  border: 1px solid white;
  border-radius: 0;
  padding: 20px;
  text-decoration: none;
  cursor: pointer;

  &:hover {
    background-color: var(--color-white);
    color: var(--color-black);
  }
}
