@import "../../core/styles/index.scss";

.team-section {
  color: var(--color-black);
  background-color: var(--color-white);
  overflow: hidden;

  &__headline {
    padding: 60px 0 0;
    font-size: var(--font-size-xl);
    text-align: center;
    font-weight: 800;
    text-transform: uppercase;
  }

  &__text {
    font-family: "Poppins", sans-serif;
    padding: 0 0 60px;
    text-align: center;
    font-weight: 200;
  }

  &__tile-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    padding: 0 10px;
    margin: 0 auto;
    width: 100%;
    transition: width 0.4s ease-in-out;

    &.is-open {
      width: 50%;
    }
  }

  &__tile-image {
    z-index: 1;
    margin-right: 10px;
  }

  &__slider {
    position: relative;
    width: 0;
    height: 0;
    transition: height 0.4s ease-in-out, width 0.4s ease-in-out;
    transform: translateY(-33px);

    &.is-active {
      width: 100%;
      height: 100%;
    }
  }

  &__slider-close {
    border: none;
    background: none;
    position: absolute;
    z-index: 3;
    cursor: pointer;
  }

  &__slider-inner {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    height: 100%;
    padding: 10% var(--page-margin-inner);

    &.is-green {
      background: lightgreen;
    }
    &.is-purple {
      background: purple;
    }
    &.is-blue {
      background: blue;
    }

    &__headline {
      font-size: var(--font-size-m);
    }

    &__image {
      margin-right: 40px;
    }

    &__text-wrapper {
      width: 100%;
      text-align: left;
    }

    &__cta {
      @include cta-white();

      background-color: transparent;
    }
  }

  .modal {
    .modal-close {
      border: none;
      background: none;
      position: absolute;
      top: 5px;
      right: 5px;
      z-index: 3;
      cursor: pointer;
      padding: 0;
      width: 36px;
      height: 36px;

      svg {
        width: 36px;
        height: 36px;
      }
    }

    .modal-image {
      margin-right: 30px;
    }

    .modal__form-wrapper {
      width: 100%;
    }
  }
}
