.mail-section {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  padding: 60px var(--page-margin) 60px 0;
  color: var(--color-white);

  &__form-wrapper {
    display: flex;
    flex-direction: column;
  }

  &__image {
    margin-left: -15px;
  }

  &__mail-headline {
    font-size: var(--font-size-m);
  }
}
