@import "../../core/styles/index.scss";

.stage {
  position: fixed;
  top: 50px;
  height: var(--height-stage);
  z-index: 1;
  width: 100%;

  &__image {
    height: 100%;
    object-fit: cover;
    background-position: center center;

    img {
      height: 100%;
      max-width: none;
      width: auto;
    }
  }

  &__inner-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    width: 100%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
  }

  &__headline {
    font-size: var(--font-size-xl);
    color: var(--color-white);
    text-align: center;
    text-transform: uppercase;
  }

  &__cta {
    @include cta-white();
  }
}
