@import "../../core/styles/index.scss";

.mail-form {
  display: flex;
  flex-direction: column;

  &__input-group {
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
  }

  &__input {
    color: var(--color-white);
    padding: 0 0 5px;
    background: transparent;
    border: none;
    border-bottom: 1px solid white;

    &.is-textarea {
      resize: vertical;
    }
  }

  &__label {
    font-size: var(--font-size-xs);
  }

  &__submit {
    @include cta-white();

    background: transparent;
  }
}
