@import "../../core/styles/index.scss";

.header {
  display: flex;
  position: fixed;
  flex-wrap: nowrap;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background: var(--color-white);
  height: var(--height-header);
  width: 100%;
  top: 0;
  z-index: 3;

  &__headline {
    color: var(--color-white);
    font-weight: var(--font-weight-bold);
    font-size: var(--font-size-m);
  }

  &__nav {
    height: 100%;
  }

  &__nav-list {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: center;
    align-items: center;
    list-style: none;
    height: 100%;
    margin: 0;
    padding: 0;
  }

  &__nav-list-item {
    margin-right: 3.6rem;
  }

  &__nav-link {
    @include link-default();
  }

  &__menu-toggle {
    border: none;
    border-radius: 0;
    background: #000;
    color: #fff;
    cursor: pointer;
    display: inline-block;
    padding: 1em 1.5em;
    text-transform: uppercase;
    transition: all 0.25s ease-in-out;
    font-size: 0.875em;
    padding: 1em;

    &:focus {
      outline: thin dotted;
      outline-offset: -2px;
    }

    &.is-open {
      .line-1 {
        transform: rotate(45deg) translateY(0) translateX(0);
      }
      .line-2 {
        opacity: 0;
      }
      .line-3 {
        transform: rotate(-45deg) translateY(0em) translateX(0em);
      }
    }

    .screen-reader-text {
      clip: rect(1px, 1px, 1px, 1px);
      position: absolute;
      height: 1px;
      width: 1px;
      overflow: hidden;
    }
  }

  &__menu-toggle-icon {
    display: inline-block;
    fill: currentColor;
    height: 1em;
    width: 1em;
    vertical-align: middle;
    position: relative;
    top: -0.0625em;
    width: 2em;
    height: 2em;
    top: 0;

    .line {
      opacity: 1;
      transform: rotate(0) translateY(0) translateX(0);
      transform-origin: 1em 1em;
      transition: transform 0.3s ease-in-out, opacity 0.2s ease-in-out;
    }

    .line-1 {
      transform-origin: 1em 2.5em;
    }

    .line-3 {
      transform-origin: 1em 4.5em;
    }
  }

  &__offset-nav {
    position: absolute;
    left: 100vw;
    top: 3px;
    height: 100vh;
    width: 33vw;
    background: lightcoral;
    transition: transform 0.4s ease-in-out;

    &.is-open {
      transform: translateX(-64vw);
    }

    .header__nav-list {
      flex-direction: column;
      justify-content: flex-start;
    }
  }
}
