.modal {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transform: translateY(-200vh);
  transition: transform 0.4s ease-in-out;
  z-index: 3;

  &.is-active {
    transform: translateY(0);
  }

  &__inner {
    position: absolute;
    top: 10%;
    left: 10%;
    right: 10%;
    bottom: 10%;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    overflow: scroll;
    flex-wrap: nowrap;
    padding: 50px;
    background: rgb(0, 0, 0);
    background: linear-gradient(
      90deg,
      rgba(0, 0, 0, 1) 0%,
      rgba(0, 0, 0, 1) 34%,
      rgba(45, 45, 45, 1) 100%
    );
  }
}
